body {
  background: #FFD9CE;
}

.container {
  display: flex;
  flex-wrap: wrap;
}
.btn-close{
  outline: none;
 transition: all 0.5s ease;
    position: absolute;
    background-color: #07a5e7;
    padding: 1.5px 7px;
    left: 0;
    top:0;
    margin-left: -9px;
    margin-top: -9px;
    border-radius: 50%;
    border: 2px solid #fff;
    color: white;
    -webkit-box-shadow: -4px -2px 6px 0px rgba(0,0,0,0.1);
    -moz-box-shadow: -4px -2px 6px 0px rgba(0,0,0,0.1);
    box-shadow: -3px 1px 6px 0px rgba(0,0,0,0.1);
}

.btn-close:hover {
    background-color: rgb(226, 128, 111);
    color: #fff;
}


