.btn-green {
   position: absolute;
    font-size: 3vw;
 padding: 1.5px 7px;
    right: 0;
    margin-left: -9px;
    margin-top: -9px;
    text-align: center;
    color: white;
    margin: 0 auto;
    bottom: 0;
    border: none;
    outline: none;
    cursor: pointer;
    overflow: hidden;
    transition: transform 0.4s ease-in-out;
    background: #1abc9c;
    padding:3px;
    border-radius: 5px;
}
.btn-green .icon {
    width: 15px;
    height: 15px;
    transition: all 0.4s ease-in-out;
}
.btn-green:hover .icon {
    transform: rotate(360deg) scale(1.2);
}
.btn-green:hover {
    background-color: #1abc70;
    color: #fff;
}